import '../css/components.css';

import v2 from '../img/demo/5.png';
// import v3 from '../img/demo/1.png';
import fullStarIcon from '../img/icons/fullstar.png';
import halfStarIcon from '../img/icons/halfstar.png';
import pinIcon from '../img/icons/pin.png';
import { Link } from 'react-router-dom';


import defaultMgs from '../translations/DefaultMessage';
import {FormattedMessage} from 'react-intl';

export default function BestDeals() {
    return (

        <>

            {/* desktop version  */}
            <div className="container">
                <div className="heading">
                <FormattedMessage 
                    id="BestDeals"
                    defaultMessage={defaultMgs.Mgs.BestDeals}
                />
                </div>

                <div className="BestDealsCont">

                    {/* map iterator here */}
                    <Link to="hotelInfo" className="BestDealsSin">
                        <img src={v2} alt="" />
                        <p className="BestSinP1">Queens Hotel</p>
                        <p className="BestSinP2">
                        <FormattedMessage 
                            id="Wembly"
                            defaultMessage={defaultMgs.Mgs.Wembly}
                        />, 
                        <FormattedMessage 
                            id="London"
                            defaultMessage={defaultMgs.Mgs.London}
                        /></p>
                        <div className="BestDealsTxt0">
                            <div className="BestDealsTxt01">
                                <p className="BestSinP3">
                                    <img src={pinIcon} alt="" />
                                     2.0 
                                     <FormattedMessage 
                                        id="kmToCity"
                                        defaultMessage={defaultMgs.Mgs.kmToCity}
                                    /></p>
                                <p className="BestSinP4">
                                    <span>
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={halfStarIcon} alt="" />
                                    </span>
                                </p>
                            </div>
                            <div className="BestDealsTxt02">
                                <p className="BestSinP5">$200</p>
                                <p className="BestSinP6">/
                                <FormattedMessage 
                                    id="perNight"
                                    defaultMessage={defaultMgs.Mgs.perNight}
                                />
                                    </p>
                            </div>
                        </div>
                    </Link>

                    <Link to="hotelInfo" className="BestDealsSin">
                        <img src={v2} alt="" />
                        <p className="BestSinP1">Queens Hotel</p>
                        <p className="BestSinP2">
                        <FormattedMessage 
                            id="Wembly"
                            defaultMessage={defaultMgs.Mgs.Wembly}
                        />, 
                        <FormattedMessage 
                            id="London"
                            defaultMessage={defaultMgs.Mgs.London}
                        /></p>
                        <div className="BestDealsTxt0">
                            <div className="BestDealsTxt01">
                                <p className="BestSinP3">
                                    <img src={pinIcon} alt="" />
                                     2.0 
                                     <FormattedMessage 
                                        id="kmToCity"
                                        defaultMessage={defaultMgs.Mgs.kmToCity}
                                    /></p>
                                <p className="BestSinP4">
                                    <span>
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={halfStarIcon} alt="" />
                                    </span>
                                </p>
                            </div>
                            <div className="BestDealsTxt02">
                                <p className="BestSinP5">$200</p>
                                <p className="BestSinP6">/
                                <FormattedMessage 
                                    id="perNight"
                                    defaultMessage={defaultMgs.Mgs.perNight}
                                />
                                    </p>
                            </div>
                        </div>
                    </Link>

                    <Link to="hotelInfo" className="BestDealsSin">
                        <img src={v2} alt="" />
                        <p className="BestSinP1">Queens Hotel</p>
                        <p className="BestSinP2">
                        <FormattedMessage 
                            id="Wembly"
                            defaultMessage={defaultMgs.Mgs.Wembly}
                        />, 
                        <FormattedMessage 
                            id="London"
                            defaultMessage={defaultMgs.Mgs.London}
                        /></p>
                        <div className="BestDealsTxt0">
                            <div className="BestDealsTxt01">
                                <p className="BestSinP3">
                                    <img src={pinIcon} alt="" />
                                     2.0 
                                     <FormattedMessage 
                                        id="kmToCity"
                                        defaultMessage={defaultMgs.Mgs.kmToCity}
                                    /></p>
                                <p className="BestSinP4">
                                    <span>
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={fullStarIcon} alt="" />
                                        <img src={halfStarIcon} alt="" />
                                    </span>
                                </p>
                            </div>
                            <div className="BestDealsTxt02">
                                <p className="BestSinP5">$200</p>
                                <p className="BestSinP6">/
                                <FormattedMessage 
                                    id="perNight"
                                    defaultMessage={defaultMgs.Mgs.perNight}
                                />
                                    </p>
                            </div>
                        </div>
                    </Link>



                </div>

            </div>

        </>

    );
}