import '../css/Footer.css';

import defaultMgs from '../translations/DefaultMessage';
import {FormattedMessage} from 'react-intl';

export default function Footer() {
    return(

        <div className="footerContainerMain">
            
            <div className="footerCont">
                <a href={"@"}>
                <FormattedMessage 
                    id="about"
                    defaultMessage={defaultMgs.Mgs.about}
                />
                </a>
                <a href={"@"}>
                <FormattedMessage 
                    id="community"
                    defaultMessage={defaultMgs.Mgs.community}
                />
                </a>
                <a href={"@"}>
                <FormattedMessage 
                    id="host"
                    defaultMessage={defaultMgs.Mgs.host}
                />
                </a>
                <a href={"@"}>
                <FormattedMessage 
                    id="support"
                    defaultMessage={defaultMgs.Mgs.support}
                />
                </a>
            </div>

        </div>

    );
}