export default function Examples () {

    const [guestAdult, setGuestAdult] = useState(0);
    const guestVal = sign => {

        if (sign === "+") {
            if (guestAdult >= 0) {
                setGuestAdult(preV => preV + 1);
            }
        } else {
            if (guestAdult > 0) {
                setGuestAdult(preV => preV - 1);
            }
        }
    }

    return(
        <>
        
            <button onClick={() => guestVal("-")}>-</button>
            <span>{guestAdult}</span>
            <button onClick={() => guestVal("+")}>+</button>
        
        </>
    )
}


