import { useParams } from 'react-router-dom';
import LoginBanner from '../img/banners/undraw_enter_uhqk.png';
import downArrow from '../img/icons/down-arrow.png';
import LockImg from '../img/icons/lock.png';
import { useState, useEffect } from 'react';

export default function EmailValidation() {

    const {token} = useParams();
    const [isTokenValid, setIsTokenValid] = useState({
        isToken: true,
        token: ""
    });

    const [newPass, setNewPass] = useState({
        newPassword: "",
        confirmPass: ""
    });

    const submitResetPass = () => {
        if (newPass.newPassword === "") {
            alert("Enter new password ...");
        } else if (newPass.confirmPass === "") {
            alert("Enter confirm password ..." + token);
        } else {
            alert("done");
        }
    }

    useEffect(() => {
        console.log(newPass);
    }, [newPass]);

    return(
        <>
        
            {/* Login view */}
            {isTokenValid.isToken && (
                
                <div className="signupPage">
                    <div className="sign0">
                        <div className="sign01">
                            {/* 1st row */}
                            <div className="sign011"><img style={{marginTop: '80px'}} src={LoginBanner} alt="" /></div>
                        </div>
                        <div className="sign02">

                            {/* 2nd row */}
                            <div className="sign021" style={{marginTop: '100px'}}>Reset Your Password</div>
                            <div className="sign022">
                                <span className="sign0221"><img src={LockImg} alt="" /></span>
                                <span className="sign0222"><input type="password" value={newPass.newPassword} onChange={e => setNewPass({...newPass, newPassword: e.target.value})} placeholder="Enter new password ..." /></span>
                                <span className="sign0223"><img src={downArrow} alt="" /></span>
                            </div>
                            <div className="sign022">
                                <span className="sign0221"><img src={LockImg} alt="" /></span>
                                <span className="sign0222"><input type="password" value={newPass.confirmPass} onChange={e => setNewPass({...newPass, confirmPass: e.target.value})} placeholder="Enter confirm password ..." /></span>
                                <span className="sign0223"><img src={downArrow} alt="" /></span>
                            </div>
                            <div className="sign023"><button style={{marginTop: '30px'}} onClick={submitResetPass}>Reset</button></div>
                            

                        </div>
                    </div>
                </div>

            )}

        </>
    );
}