import HeaderMin from '../components/HeaderMin';
import '../css/components.css';
import varimg from '../img/banners/ad.jpg';

export default function CarInfo () {
    return (

        <>
            <HeaderMin />

            <div className="carInfo0">
                <div className="carInfo01"><img src={varimg} alt="" /></div>
                <div className="carInfo02">Volkswagen Polo (Petrol)</div>
                <div className="carInfo03">4.7 * (250 reviews)</div>
                <div className="carInfo04">
                    <div className="carInfo041">Seats</div><div className="carInfo042">5</div>
                </div>
                <div className="carInfo04">
                    <div className="carInfo041">Doors</div><div className="carInfo042">4</div>
                </div>
                <div className="carInfo04">
                    <div className="carInfo041">Gas</div><div className="carInfo042">Reg</div>
                </div>
                <div className="carInfo05"><button>Add Car</button></div>
            </div>

        </>

    );
}